/* Global Styles */
body {
    font-family: Arial, sans-serif;
    background-color: #343541;
    color: white;
    margin: 0;
    padding: 0;
    display: flex;
    height: 100vh;
    overflow: hidden;
    font-family: 'Inter', sans-serif;
  }
  
  /* Chat Container */
  .chat-container {
    display: flex;
    width: 100vw;
    height: 100vh;
  }
  
  /* Sidebar */
  .sidebar {
    width: 300px;
    background: #202123;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: width 0.3s ease-in-out;
    overflow: hidden;
  }
  
  .sidebar.collapsed {
    width: 50px;
    padding: 10px;
  }
