  /* Sidebar Header (Hamburger & New Chat Button Row) */
.sidebar-header {
    display: flex;
    align-items: center;
    justify-content: space-between; /* Align left and right */
    width: 100%;
    margin-bottom: 15px;
}

/* Hamburger Button */
.toggle-btn {
    background: none;
    border: none;
    font-size: 24px;
    color: white;
    cursor: pointer;
    margin-bottom: 20px;
}

