  /* Hide text when sidebar is collapsed */
  .sidebar.collapsed .profile,
  .sidebar.collapsed .new-chat-btn,
  .sidebar.collapsed .chat-history {
    display: none;
  }
  
  .profile {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .profile img {
    width: 50px;
    border-radius: 10%;
    width: 50%;
    /* margin-top: 100px; */
    margin-bottom: 10px;
  }
  
  .new-chat-btn {
    width: 15%;
    margin-left: 150px;
    padding: 5px;
    font-size: 25px;
    margin-bottom: 20px;
    background: #444;
    color: rgb(221, 216, 216);
    border: none;
    cursor: pointer;
    border-radius: 5px;
  }
  .search-btn {
    width: 15%;
    padding: 5px;
    font-size: 25px;
    margin-bottom: 20px;
    background: #444;
    color: rgb(221, 216, 216);
    border: none;
    cursor: pointer;
    border-radius: 5px;
  }
  
  .new-chat-btn:hover, .search-btn:hover {
    background: #555;
  }
  
  .chat-history {
    width: 100%;
    flex-grow: 1;
    text-align: left;
    overflow-y: auto;
  }
  
  .chat-item {
    padding: 10px;
    cursor: pointer;
    /* background: ; */
    /* background: #3C4043; */
    /* background: #292b2f; */
    transition: background 0.5s ease-in-out;
    margin-bottom: 5px;
    border-radius: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .chat-item.active {
    background: #54585c;
  }

  .chat-item:hover {
    background: #8794a0;
  }
  
  .delete-btn {
    background: none;
    border: none;
    color: red;
    cursor: pointer;
    font-size: 18px;
  }




  .stop-btn {
    background-color: #ef4444;
    color: white;
    border: none;
    padding: 6px 12px;
    margin-bottom: 8px;
    border-radius: 6px;
    cursor: pointer;
    font-weight: bold;
    position: absolute;
    right: 10px;
    top: -40px;
  }

  
  
  

/* Smoothly show and hide the delete button */
.delete-btn {
  background: none;
  border: none;
  cursor: pointer;
  opacity: 0;  /* Start hidden */
  transform: translateX(10px); /* Move slightly right */
  transition: opacity 0.3s ease, transform 0.3s ease;
}

.chat-item:hover .delete-btn {
  opacity: 1;  /* Fade in */
  transform: translateX(0); /* Move back */
}

.trash-icon {
  font-size: 18px;
  color: white;
  transition: color 0.3s ease;
}

.trash-icon:hover {
  color: red; /* Change color on hover */
}


  
/* Keep scrollbar at the right edge */
.chatbox {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: #1B1C1D;
    padding: 30px 0 40px 0;
    overflow-y: auto;
}
  
  /* Messages */

  .messages {
    flex-grow: 1;
    overflow-y: auto;
    padding-bottom: 20px;
    display: flex;
    /* width: 85%; */
    padding-left: 8%;
    padding-right: 8%;
    flex-direction: column;
    /* align-self: stretch; Stretch to full width */
    scroll-behavior: smooth;
}

    /* Custom Scrollbar */
::-webkit-scrollbar {
    width: 8px; /* Adjust width */
}

::-webkit-scrollbar-thumb {
    background: #555; /* Darker color for better contrast */
    border-radius: 10px; /* Rounded edges */
    transition: background 0.3s ease-in-out;
}

::-webkit-scrollbar-thumb:hover {
    background: #777; /* Slightly lighter on hover */
}

::-webkit-scrollbar-track {
    background: #222; /* Dark background */
    border-radius: 10px;
}

/* For chatbox specific scrollbar */
.chatbox::-webkit-scrollbar {
    width: 6px;
}

.chatbox::-webkit-scrollbar-thumb {
    background: #555;
    border-radius: 10px;
}

.chatbox::-webkit-scrollbar-track {
    background: #2c2c2e;
}

  
  /* User and bot messages */
  .message.bot {
    max-width: 75%;
    padding: 12px 16px;
    border-radius: 0 30px 30px 30px;
    margin: 5px 10px;
    font-size: 18px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    word-wrap: break-word;
    line-height: 1.4;
  }
  .message.user {
    max-width: 75%;
    padding: 12px 16px;
    border-radius: 30px 0 30px 30px;
    margin: 5px 10px;
    font-size: 18px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    word-wrap: break-word;
    line-height: 1.4;
  }

  
  .message.user {
    align-self: flex-end;
    background: #333537;
    color: #dcd7d7;
    text-align: right;
  }
  
  .message.bot {
    align-self: flex-start;
    background: #1B1C1D;
    color: #dcd7d7;
    text-align: left;
  }
  
  /* Input Area */
  .input-area {
    display: flex;
    padding: 5px;
    /* border: white 10px; */
    border-radius: 20px;
    background: #303030;
    border: 1px solid #ffffff;
    width: 80%;
    /* align-items: center; */
    align-self: center;
  }
  
  .input-area textarea {
    flex: 1;
    padding: 10px;
    border-radius: 5px;
    font-size: 20px;
    border: none;
    background: #303030;
    color: white;
    resize: none;
    outline: none;
    height: 40px;
    overflow-y: hidden;
  }
  

/* Scroll to Bottom Button (Centered in Chat Area) */
.scroll-bottom-btn {
    position: absolute;
    bottom: 120px; /* Adjust as needed */
    left: 50%;
    transform: translateX(-50%);
    background: #212121;
    color: rgb(216, 212, 212);
    border-color: white;
    border-width: 2px;
    padding: 10px 15px;
    border-radius: 50%; /* Ensures circular shape */
    cursor: pointer;
    font-size: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
}

/* Ensure the button stays inside the chat area */
.chatbox {
    position: relative;
}


  
  .scroll-bottom-btn:hover {
    background: #4d4c4c;
  }
  
  /* Auto-scroll chat */
  .chatbox::-webkit-scrollbar {
    width: 6px;
  }
  
  .chatbox::-webkit-scrollbar-thumb {
    background: #555;
    border-radius: 3px;
  }
  
  .chatbox::-webkit-scrollbar-track {
    background: #333;
  }
  
  .message.bot p {
    white-space: pre-wrap;
    word-wrap: break-word;
    overflow-wrap: break-word;
  }
  
  /* Bot "Thinking..." Message */
.message.bot.thinking {
    background: #3a3b3d;
    color: #bbb;
    font-style: italic;
    opacity: 0.8;
    animation: blink 1.5s infinite;
  }
  
  /* Blinking Effect */
  @keyframes blink {
    0% { opacity: 0.6; }
    50% { opacity: 1; }
    100% { opacity: 0.6; }
  }
  

  


  
    /* Chatbox Header (Holds Profile) */
.chatbox-header {
    position: absolute;
    top: 10px;
    right: 10px;
    display: flex;
    align-items: center;
  }
  
  /* Profile Container */
  .profile-container {
    position: relative; /* Needed for dropdown positioning */
    display: inline-block;
  }
  
  /* Profile Icon */
  .profile-icon {
    width: 40px;
    height: 40px;
    background-color: #333;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    font-size: 18px;
    cursor: pointer;
  }
  
  /* Dropdown Menu */
  .dropdown {
    padding: 10px;
    position: absolute;
    top: 65px; /* Below profile icon */
    right: 20px;
    background: #2c2b2b;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    width: 250px;
    z-index: 1000;
  }
  
  /* Dropdown Links */
  .dropdown ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .dropdown li {
    padding: 15px;
    text-align: left;
    cursor: pointer;
    color: #cfcccc;
    font-size: 18px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    transition: background 0.2s;
  }
  
  .dropdown li:hover {
    /* padding: 10px; */
    background: #444343;
  }
  
  

  .sidebar-ad {
    position: absolute;
    bottom: 0;
    width: 20%;
    height: 25%;
    background: #1e1f22;
    padding: 10px;
    text-align: center;
    z-index: 2;
    border-top: 1px solid #333;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  /* .sidebar-ad ins.adsbygoogle {
    display: block !important;
    width: 100% !important;
    height:auto !important;
  } */
  
  .sidebar-ad img {
    width: 100%;
    border-radius: 5px;
    transition: transform 0.3s ease;
  }
  
  .sidebar-ad img:hover {
    transform: scale(1.05);
  }
  
  .close-ad {
    background: none;
    border: none;
    color: rgb(0, 0, 0);
    font-size: 36px;
    cursor: pointer;
    position: absolute;
    top: 5px;
    z-index: 1;
    right: 5px;
  }
  
  .close-ad:hover {
    color: red;
  }

  


  
  

  
/* üîç Search Popup (Like ChatGPT) */
.search-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(5px);
  z-index: 999;
}

/* Popup Box */
.search-popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #343541;
  padding: 20px;
  width: 400px;
  border-radius: 10px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  z-index: 1000;
  animation: fadeIn 0.2s ease-in-out;
}

/* Popup Header */
.search-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 10px;
  border-bottom: 1px solid #4e4f52;
}

.search-header h2 {
  font-size: 18px;
  margin: 0;
  color: white;
}

.close-btn {
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
  color: #ffffff;
}

.close-btn:hover {
  color: #f25b5b;
}

/* Search Box */
.search-box {
  margin-top: 10px;
}

.search-box input {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  background: #40414f;
  border: 1px solid #4e4f52;
  border-radius: 5px;
  outline: none;
  color: white;
}

.search-box input::placeholder {
  color: #b4b4b4;
}

/* Search Results */
.search-results {
  list-style: none;
  padding: 0;
  margin-top: 10px;
  max-height: 200px;
  overflow-y: auto;
}

.search-results li {
  padding: 10px;
  border-bottom: 1px solid #4e4f52;
  cursor: pointer;
  transition: background 0.2s ease-in-out;
  color: white;
}

.search-results li:hover {
  background: #2a2b33;
}

.search-results li:active {
  background: #19c37d;
  color: black;
}

.no-result {
  text-align: center;
  color: gray;
  padding: 8px;
}

/* Fade-in animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translate(-50%, -55%);
  }
  to {
    opacity: 1;
    transform: translate(-50%, -50%);
  }
}





/* Responsive Styles */
/* üìå Mobile & Tablet (Max 1024px) */
@media (max-width: 1024px) {
  
    .chat-container{
      overflow: hidden;
    }
    /* Chat container: Stack items vertically */
    .chat-container {
        flex-direction: column;
        height: 100vh;
        width: 100vw;
    }

    /* Sidebar (Hidden by Default on Mobile) */
    .sidebar {
        position: fixed;
        left: -250px; /* Hide sidebar */
        width: 250px;
        height: 100vh;
        transition: left 0.3s ease-in-out;
        z-index: 1000;
        background: #202123;
        display: flex;
        flex-direction: column;
        padding: 15px;
    }

    /* Sidebar Open State */
    .sidebar.open {
        left: 0; /* Slide sidebar into view */
    }

    /* Toggle Button Position */
    .toggle-btn {
        position: fixed;
        left: 10px;
        top: 10px;
        z-index: 1100;
        background: none;
        border: none;
        font-size: 34px;
        border-radius: 20px;
        color: white;
        cursor: pointer;
    }

    /* Sidebar Header */
    .sidebar-header {
        width: 100%;
        display: flex;
        justify-content: space-between;
    }


      /* Profile Icon */
    .profile-icon {
      width: 40px;
      height: 40px;
      background-color: #333;
      color: white;
      display: flex;
      align-items: center;
      margin-right: 25px;
      margin-top: 8px;
      padding: 5px;
      justify-content: center;
      border-radius: 50%;
      font-size: 35px;
      cursor: pointer;
    }

    /* Chatbox (Takes Full Width when Sidebar is Closed) */
    .chatbox {
        width: 100%;
        padding: 15px;
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        background: #1B1C1D;
    }

    /* Messages */
    .messages {
        padding: 10px;
        flex-grow: 1;
        overflow-y: auto;
        display: flex;
        flex-direction: column;
        padding-left: 5%;
        padding-right: 5%;
        margin-top: 15%;
    }

    /* Chat Input Area */
    .input-area {
        width: 90%;
        margin: 0 auto;
        display: flex;
        padding: 8px;
        border-radius: 10px;
        background: #303030;
        border: 1px solid #ffffff;
    }

    /* Chat Textarea */
    .input-area textarea {
        width: 100%;
        font-size: 16px;
        padding: 8px;
        height: 35px;
    }

    /* Scroll to Bottom Button */
    .scroll-bottom-btn {
        bottom: 100px;
    }
}

