.otp-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #101418;
  padding: 20px;
  position: relative;
  margin: 0;
  overflow: hidden;
}

.resend-btn {
  margin-top: 10px;
  padding: 10px 20px;
  background-color: #3c76ff;
  border: none;
  color: white;
  border-radius: 5px;
  cursor: pointer;
  font-size: 15px;
  transition: 0.3s ease;
}

.resend-btn:hover {
  background-color: #2e63d1;
}


/* Optional: make body and html 100% height and remove spacing */
html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

/* OTP box */
.otp-box {
  background: #1e1f22;
  padding: 40px;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  text-align: center;
  width: 100%;
  max-width: 350px;
  box-sizing: border-box;
}

/* Headings */
h2 {
  color: #ffffff;
  font-size: 24px;
  margin-bottom: 20px;
}

/* OTP inputs container */
.otp-inputs {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-bottom: 20px;
}

/* Individual OTP input */
.otp-input {
  width: 50px;
  height: 50px;
  font-size: 24px;
  text-align: center;
  border: 1px solid #333;
  border-radius: 5px;
  background: #26282c;
  color: #fff;
}

.otp-input:focus {
  outline: 2px solid #3c76ff;
}

/* Verify button */
.verify-btn {
  width: 100%;
  padding: 10px;
  background: #3c76ff;
  border: none;
  border-radius: 5px;
  color: white;
  font-size: 16px;
  cursor: pointer;
  transition: background 0.3s;
}

.verify-btn:hover {
  background: #305fdb;
}

/* Error messages */
.error-msg {
  color: red;
  font-size: 14px;
  margin-bottom: 10px;
}

/* Timer styling */
.timer {
  font-size: 16px;
  color: #939292;
  margin-bottom: 10px;
}

/* Expired message styling */
.expired-msg {
  color: red;
  font-weight: bold;
  margin-top: 10px;
}

/* Footer text */
.developed-by {
  text-align: center;
  margin-top: 20px;
  font-size: 14px;
  color: #888;
  position: absolute;
  bottom: 10px;
  width: 100%;
}

/* --- Responsive adjustments --- */

/* For smaller devices, adjust padding and font sizes */
@media (max-width: 480px) {
  .otp-box {
    padding: 20px;
    width: 90%;
  }
  
  h2 {
    font-size: 20px;
  }
  
  .otp-input {
    width: 40px;
    height: 40px;
    font-size: 20px;
  }
  
  .verify-btn {
    font-size: 14px;
    padding: 8px;
  }
  
  .timer {
    font-size: 14px;
  }
  
  .developed-by {
    font-size: 12px;
  }
}
