/* üåü ChatGPT-Style Signup Page - Full Screen üåü */

@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;600&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Inter", sans-serif;
}

body, html {
  width: 100%;
  height: 100%;
  overflow: hidden; /* Prevent scrolling */
}

.signup-container {
  display: flex;
  width: 100vw;
  height: 100vh;
  background: linear-gradient(to right, #101418, #181c20);
}

.signup-box {
  flex: 1;
  background: rgba(30, 31, 34, 0.98);
  padding: 50px;
  border-radius: 10px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.6);
  max-width: 450px;
  margin: auto;
  transition: transform 0.3s;
}

.signup-box:hover {
  transform: scale(1.02);
}

h2 {
  color: #ffffff;
  font-size: 26px;
  margin-bottom: 20px;
  font-weight: 600;
}

.input-group {
  margin-bottom: 18px;
  text-align: left;
}

.input-group label {
  display: block;
  color: #bbbbbb;
  font-size: 14px;
  margin-bottom: 6px;
}

.input-group input {
  width: 100%;
  padding: 12px;
  border: none;
  border-radius: 6px;
  background: #2b2d31;
  color: #ffffff;
  font-size: 15px;
  outline: none;
  transition: 0.3s;
}

.input-group input:focus {
  background: #33353a;
  border: 1px solid #3c76ff;
}

.signup-btn {
  width: 100%;
  padding: 12px;
  background: #3c76ff;
  border: none;
  border-radius: 6px;
  color: white;
  font-size: 17px;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s;
}

.signup-btn:hover {
  background: #2c5cd8;
}

.error-msg {
  color: red;
  font-size: 14px;
  margin-bottom: 12px;
}

.login-link {
  color: #bbb;
  font-size: 14px;
  margin-top: 15px;
}

.login-link a {
  color: #3c76ff;
  text-decoration: none;
  font-weight: 500;
}

.login-link a:hover {
  text-decoration: underline;
}

/* üåü Right Side - Image & Text üåü */
.signup-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  padding: 40px;
  max-width: 50%;
}

.signup-info h1 {
  font-size: 34px;
  margin-bottom: 10px;
  font-weight: 700;
}

.signup-info p {
  font-size: 18px;
  margin-bottom: 20px;
  color: #bbb;
}

.signup-info img {
  max-width: 60%;
  height: auto;
  animation: float 3s ease-in-out infinite;
}


.developed-by {
  text-align: center;
  margin-top: 20px;
  margin-left: 5%;
  font-size: 14px;
  color: #888;
  position: absolute;
  bottom: 10px;
  width: 100%;
}


/* Floating Animation */
@keyframes float {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-8px);
  }
}

/* üì± Responsive Design */
@media (max-width: 900px) {
  .signup-container {
    flex-direction: column;
  }

  .signup-box {
    width: 90%;
    margin-bottom: 20px;
  }

  .signup-info {
    max-width: 90%;
    padding: 20px;
  }

  .signup-info img {
    max-width: 60%;
  }
}
